.current-paginate-button {
  padding: 10px;
  border-radius: 60%;
  padding: 5px 10px;
  font-size: 17px;
  background-color: white;
}
.paginate-button {
  padding: 8px;
  font-size: 17px;
  border-radius: 50%;
  background-color: white;
  border: none;
  transition: all 0.2s ease-out;
}
.paginate-button:hover {
  padding: 10px;
  font-size: 17px;
  border-radius: 70%;
  background-color: white;
  border: none;
  color: red;
}
