.all {
  background: white(141, 136, 136, 0.363);
  width: 100vw;
  height: 92.2vh;
}
.body-detail {
  width: 70rem;
  margin: auto;
  border: salmon 3px solid;
  background: whitesmoke;
}
.recipe-title {
  border: solid 3px black;
  width: 40rem;
  height: 4rem;
  float: left;
  padding-top: 20px;
  color: rgb(90, 224, 224);
}
.body-title-img {
  width: 70rem;
}
.img-detail {
  width: 28rem;
  height: 15rem;
  float: right;
}
.recipe-image {
  width: 17rem;
  height: 13rem;
  border-radius: 20px;
}
.body-recipe-summary {
  margin: auto;
  text-decoration: none;
  list-style-type: none;
}
.detail-diet-steps {
  border-top: black solid 2px;
  display: flex;
}
.diets-detail {
  border: solid 2px pink;
  width: 14rem;
}
.steps-detail {
  padding: 0;
  width: 35rem;
  float: left;
}
.step-box {
  text-align: justify;
}
