@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&family=Raleway:ital,wght@1,100;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.div-landing {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-image: url(https://img4.goodfon.com/wallpaper/nbig/7/50/guilherme-gomes-chef-3d-shef-povar-eda.jpg);
}
.title-landing {
  font-size: 34px;
  margin-top: 300px;
  background: transparent;
  border-radius: 10px;
  border: none;
}
.title-landing:hover {
  background-color: tomato;
}

.all {
  background: white(141, 136, 136, 0.363);
  width: 100vw;
  height: 92.2vh;
}
.body-detail {
  width: 70rem;
  margin: auto;
  border: salmon 3px solid;
  background: whitesmoke;
}
.recipe-title {
  border: solid 3px black;
  width: 40rem;
  height: 4rem;
  float: left;
  padding-top: 20px;
  color: rgb(90, 224, 224);
}
.body-title-img {
  width: 70rem;
}
.img-detail {
  width: 28rem;
  height: 15rem;
  float: right;
}
.recipe-image {
  width: 17rem;
  height: 13rem;
  border-radius: 20px;
}
.body-recipe-summary {
  margin: auto;
  text-decoration: none;
  list-style-type: none;
}
.detail-diet-steps {
  border-top: black solid 2px;
  display: flex;
}
.diets-detail {
  border: solid 2px pink;
  width: 14rem;
}
.steps-detail {
  padding: 0;
  width: 35rem;
  float: left;
}
.step-box {
  text-align: justify;
}

.container-nav {
  height: 3rem;
  background-color: black;
  margin-bottom: 3px;
}
.ul-nav {
  background: black;
  list-style: none;
  display: flex;
  justify-content: space-around;
  margin: auto;
  width: 70rem;
  height: 2rem;
  padding-top: 0.7rem;
}
.hamburger {
  font-size: 25px;
  color: white;
  margin: auto;
  display: block;
  text-align: left;
}
.li-nav {
  font-size: 20px;
  color: white;
  letter-spacing: 2px;
  display: block;
  text-align: left;
  outline: none;
  background-color: black;
  border: none;
  border-radius: 20px;
  transition: 1s all;
  cursor: pointer;
}
.li-nav:hover {
  border-radius: 20px;
}

.inputsearch {
  border-radius: 8rem;
  margin-right: 0.7rem;
  padding-left: 0.5rem;
  padding-right: 6rem;
  border-style: none;
}
.form {
  display: flex;

  height: 1.5rem;
}
.faSearch-icon {
  font-size: 20px;
  background-color: black;
  color: white;
}
a:visited {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: none;
  color: black;
}
a:focus {
  text-decoration: none;
  color: black;
}
a:hover,
a:active {
  text-decoration: none;
  color: black;
}
.search-button {
  font-size: 0.8rem;
  color: white;
  cursor: pointer;
  background-color: black;
  border: none;
  outline: none;
}

.form-total {
  background: url(https://i.pinimg.com/564x/9c/3d/48/9c3d48926c7e94966def78c6e702f919.jpg);
  height: 100vh;
}
.body-form {
  margin: auto;

  width: 30rem;
  background-color: rgba(128, 128, 128, 0.616);
  display: block;
  border: 20px;
}
.diets-form-include {
  background-color: #00b347;
}
.diets-form-no {
  background-color: blue;
}

.container-form-title {
  display: block;
}
.form-title {
  margin: 10px 30px;
  outline: none;
  height: 1.4rem;
  width: 50%;
  padding: 2.5px 10px;
  border: none;
  border-radius: 10px;
}
.diets-container-form {
  border: 2px solid red;
  display: block;
  width: 15rem;
}
.diets-form {
  width: 90%;
  height: 2rem;
}
.container-select-diets {
  display: block;
  justify-content: center;
}

.select-diets-form {
  width: 30rem;
  height: 2rem;
  border-radius: 5px;
  background-color: #161616;
  color: #fff;
  text-align: center;
  font-size: 14px;
}
.select-diets-form:after {
  content: "\025be";
  display: table-cell;
  padding-top: 7px;
  text-align: center;
  width: 30px;
  height: 30px;
  background-color: #d9d9d9;
  position: absolute;
  top: 0;
  right: 0px;
  pointer-events: none;
}
.button-form {
  background-color: #757575;
  border-radius: 0.5rem;
  box-shadow: 0 0 12px rgb(0 0 0 / 15%);
  border: none;
  display: flex;
  float: left;
  cursor: pointer;
  color: #fff;
  height: 2rem;
  width: 100%;
  display: table-cell;
  font-size: 15px;
}
.input-steps-form {
  width: 95%;
  border-radius: 5px;
  height: 1.4rem;
}
.button-submit-form {
  height: 3rem;
  width: 100%;
  margin-top: 2rem;
  background-color: #063e7a;
  display: table-cell;
  border-radius: 10px;
  outline: none;
  border: none;
  color: #fff;
  font-size: 15px;
}

.container-home {
  background: white;
  margin: auto;
}
.container-home-dos {
  background-color: white;
  height: 92.4vh;
}
.container-select {
  padding-top: 10px;
  display: flex;
  justify-content: center;
  background: white;
}
.select-home {
  width: 9rem;
  height: 1.5rem;
  background: whitesmoke;
  border: none;
  font-size: 14px;
  margin: 0px 10px;
}
.select-home:after {
  content: "\025be";
  display: table-cell;
  padding-top: 7px;
  text-align: center;
  width: 30px;
  height: 30px;
  background-color: #d9d9d9;
  position: absolute;
  top: 0;
  right: 0px;
  pointer-events: none;
}
.button-favs {
  padding: 5px 10px;
  background: whitesmoke;
  border: none;
  cursor: pointer;
}

.card {
  display: inline-flex;
  justify-content: space-between;
  margin: auto;
}
.container-title {
  width: 15rem;
  height: 3.3rem;
}
.card-title {
  font-family: "Raleway", sans-serif;
  color: #5e3d9f;
  font-size: 1rem;
  letter-spacing: 1.4px;
  text-align: center;
}
.card-body {
  width: 15rem;
  height: 22rem;
  margin: 15px 15px;
  font-family: "Raleway", sans-serif;
  letter-spacing: 1.4px;
  background-color: whitesmoke;
  text-align: center;
  align-items: center;
  border: 1px solid;
  box-shadow: 5px 5px 5px grey;
  border-radius: 5px;
}
.card-img {
  width: 15rem;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  height: 160px;
  text-align: center;
  background-position: center;
  background-size: cover;
  margin: 0;
}
.card-button {
  font-family: "Noto Sans JP", sans-serif;
  font-family: "Staatliches", cursive;
  padding: 12px 50px 12px 50px;
  outline: none;
  letter-spacing: 3px;
  font-size: 1.2rem;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.5);
  border: solid 2px #f32013;
  margin: auto;
  border-radius: 4px;
}
.card-button:hover {
  background-color: chocolate;
}

.container-diets {
  font-family: sans-serif;
  font-size: 15px;
  width: 15rem;
  height: 3.3rem;
}
.faStar {
  color: rgb(14, 0, 0);
}
.faStar:hover {
  color: gold;
}
.faFav {
  color: gold;
}
.container-health {
  font-family: "Courier New", Courier, monospace;
}

.current-paginate-button {
  padding: 10px;
  border-radius: 60%;
  padding: 5px 10px;
  font-size: 17px;
  background-color: white;
}
.paginate-button {
  padding: 8px;
  font-size: 17px;
  border-radius: 50%;
  background-color: white;
  border: none;
  transition: all 0.2s ease-out;
}
.paginate-button:hover {
  padding: 10px;
  font-size: 17px;
  border-radius: 70%;
  background-color: white;
  border: none;
  color: red;
}

.App {
  text-align: center;
}
* {
  margin: 0;
  padding: 0;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

